import { EOL } from 'os';
import { escapeRegExp } from 'lodash';
import * as path from 'path';

interface IModule {
  issuer: string;
}

interface IContext {
  _module: IModule;
  emitWarning: (warning: string) => void;
  request: string;
  context: string;
}

export interface IIdentifier {
  /**
   * Export the base package.
   */
  exportPackage?: boolean;

  /**
   * A map of exports on the base package to the names to re-export.
   */
  exportMap?: { [identifier: string]: string };

  /**
   * The name of a single export from the base package. This option is mutually exclusive to the others.
   */
  export?: string;
}

abstract class LoaderBase {
  public getWebpackLoader(): (content: string) => string {
    const self: LoaderBase = this;
    return function(content: string): string {
      const context: IContext = this as IContext;
      const request: string = context.request;
      return self._processRequest(request, context);
    };
  }

  protected abstract get filename(): string;
  protected abstract get packageFriendlyName(): string;
  protected abstract get packageName(): string;
  protected abstract getIdentifier(path: string): IIdentifier;

  protected get shouldExportBasePackage(): boolean {
    return false;
  }

  protected get externalPackageName(): string {
    return this.packageName;
  }

  private _processRequest(request: string, context: IContext): string {
    const requestSections: string[] = request.split('!');
    const packagePath: string = requestSections[requestSections.length - 1];
    const posixPackagePath: string = packagePath.replace(/\\/g, '/');

    const simplifiedPathsRegExp: RegExp = new RegExp(`(?:\/${escapeRegExp(this.packageName)}\/lib\/)(.+)`);
    const simplifiedPaths: string[] = posixPackagePath.match(simplifiedPathsRegExp);

    const lines: string[] = [];

    if (simplifiedPaths) {
      const identifiersPath: string = simplifiedPaths[1].trim().match(/(?:\/*)(.+)(?:\.js)$/)[1];
      const identifiers: IIdentifier = this.getIdentifier(identifiersPath);

      lines.push(`// Loading ${this.packageName}/${simplifiedPaths[1]}`);

      if (identifiers) {
        lines.push(...[
          `var package = require('${this.externalPackageName}');`
        ]);

        if (identifiers.export) {
          lines.push(...[
            `module.exports = package.${identifiers.export};`
          ]);
        } else {
          lines.push(...[
            `module.exports = ${this.shouldExportBasePackage || identifiers.exportPackage ? 'package' : '{}'};`
          ], ...Object.keys(identifiers.exportMap || {}).map((identifier: string): string =>
            `module.exports.${identifier} = package.${identifiers.exportMap[identifier]};`
          ));
        }
      } else {
        // We didn't match a well-defined export, so we should emit a warning and pass through
        let issuer: string = context._module.issuer;
        if (issuer) {
          issuer = issuer.substr(issuer.lastIndexOf('!') + 1);
        }

        context.emitWarning(`${this.packageFriendlyName} path "${simplifiedPaths[0]}" does not correspond to a ` +
                            `well-defined export. Large portions of ${this.packageName} may be unintentionally ` +
                            `bundled. This request originated in "${issuer || '-- UNKNOWN MODULE --'}".`);

        lines.push(...this._getPassthroughLines(requestSections));
      }
    } else {
      // If we're here, this isn't a reference to the package we care about, so we should pass through
      lines.push(...this._getPassthroughLines(requestSections));
    }

    return lines.join(EOL);
  }

  private _getPassthroughLines(requestSections: string[]): string[] {
    const filename: string = path.basename(this.filename);
    const filenameRegExp: RegExp = new RegExp(escapeRegExp(filename), 'i');

    // Remove this loader from the loader chain to make sure we don't end up in an infinite loop
    const passthroughRequestSections: string[] =
      requestSections.filter((section: string) => !section.match(filenameRegExp));

    // Use "!!" prefix to disable all configured loaders because they're already specified in the remaining request.
    return [
      `module.exports = require(${JSON.stringify(`!!${passthroughRequestSections.join('!')}`)});`
    ];
  }
}

export default LoaderBase;
